<template>
    <mt-header fixed :title="title">
        <router-link to="" slot="left">
            <mt-button icon="back" @click="fh"></mt-button>
        </router-link>
    </mt-header>
</template>
<style lang="scss">
    .mint-header-title,.mintui.mintui-back {
        font-size: .36rem;
        color: #000;
    }
</style>
<script>
    export default {
        name: "header-return",
        props: ['title'],
        methods:{
            fh(){
                this.$emit('back','');
            }
        }
    }
</script>

<style scoped>

</style>



