<template>
    <div class="showLogin">
        <!--header组件-->
        <headeReturn :title="title" @back="back"></headeReturn>
        <div class="from">
            <div class="phoneNumber">
                <i><img src="./phone.png" width="0.36rem" height="0.52rem" alt=""></i>
                <input class="telMsg" type="tel" maxlength="11" placeholder="请填写您的手机号"
                                                            :value="values" v-model="values"
                                                            @input="checkVal()">
            </div>
            <div class="msgNumber">
                <div><i><img src="./password.png" width="0.36rem" height="0.36rem" alt=""></i>
                    <input type="number" placeholder="请填写短信验证码" maxlength="6" class="msgNumberInp"
                           onkeyup="this.value=this.value.replace(/[^0-9-]+/,'');"
                           oninput="if(value.length>6)value=value.slice(0,6)" @input="checkVal()"></div>

                <button disabled="disabled" class="msgNumberBtn" @click="msgCode" v-if="isShow">发送验证码</button>
                <button class="msgNumberBtn" v-else>重新获取 ({{countTime}}S)</button>
            </div>
            <input type="submit" value="登录" class="loginBtn" @click="login" disabled="disabled"
                   ondblclick="return false;">
        </div>
        <div id="loginSeccess" style="display: none;"></div>

    </div>
</template>
<style rel="stylesheet/stylus" lang="scss">
    .showLogin {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding: 0 .4rem;
        background: url("./per-bg.png") top center no-repeat;
        background-size: 7.5rem 13.34rem;
        .public-header {
            margin-top: .1rem;
            height: 0.8rem;
            line-height: 0.8rem;
            text-align: center;
            display: flex;
            align-items: center;
            .return {
                width: .19rem;
                height: .34rem;
            }
            .title {
                flex: 1;
                font-size: 0.36rem;
                color: #000000;
            }
        }
        .from {
            position: absolute;
            top: 5rem;
            width: 6.7rem;
            overflow: hidden;
            display: flex;
            box-sizing: border-box;
            padding: 0.18rem .25rem 1.36rem;
            flex-direction: column;
            background: #ffffff;
            border-radius: .3rem;
            box-shadow: 0 0 .1rem #f6a32b;
            .phoneNumber {
                display: flex;
                align-items: center;
                width: 100%;
                height: 1.42rem;
                border-bottom: 1px solid #eeeeee;
                i {
                    width: .36rem;
                    height: .52rem;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
                .telMsg {
                    margin-left: .3rem;
                    font-size: .3rem;
                    height: 100%;
                    line-height: .4rem;
                    color: #adadad;
                    text-align: left;
                }
            }
            .msgNumber {
                display: flex;
                width: 100%;
                height: 1.42rem;
                border-bottom: 1px solid #eeeeee;
                align-items: center;
                justify-content: space-between;
                div{
                    display: flex;
                    align-items: center;
                    i {
                        width: .36rem;
                        height: .36rem;
                        display: inline-block;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                }

                .msgNumberInp {
                    margin-left: .3rem;
                    font-size: .3rem;
                    height: 100%;
                    line-height: .4rem;
                    color: #adadad;
                    text-align: left;
                }
                .msgNumberBtn {
                    width: 1.98rem;
                    height: .64rem;
                    font-size: .24rem;
                    border-radius: .1rem;
                    border: 1px solid #ffb900;
                    color: #ffb900;
                    background: #ffffff;
                    &[disabled = disabled] {
                        border: 1px solid #bcbcbc;
                        color: #bcbcbc;
                        background: #ffffff;
                    }
                }
            }
            .loginBtn {
                width: 6.2rem;
                height: .8rem;
                margin-top: .8rem;
                font-size: .32rem;
                text-align: center;
                background: #ffb900;
                color: #ffffff;
                border-radius: 0.2rem;
                -moz-border-radius: 0.2rem;
                -webkit-border-radius: 0.2rem;
                &[disabled = disabled] {
                    color: #ffffff;
                    background: #adadad;
                }
            }
        }
    }
</style>
<script>
    import Vue from 'vue';
    import headeReturn from '../public/header-return'
    import { Toast,Header } from 'mint-ui';

    export default {
        name: 'Login',
        data() {
            return {
                title: '登录',
                showPicCode: false,
                isLoging: false,
                isShow: true,
                countTime: '',
                codeQC: '',
                phoneNum: '',
                msgNum: '',
                personInfo: {},
                storage: '',
                errorTime: '',
                verCode: '',
                values: '',
                iClock: true
            };
        },
        methods: {
            //返回上一级
            back() {
                this.$router.back(-1);
            },
            checkMobile() {
                if (!(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.values))) {
                    Toast("请输入正确的手机号")
                    $(".msgNumberBtn").attr('disabled', true);
                    return false;
                } else {
                    $(".msgNumberBtn").attr('disabled', false);
                }
            },
            msgCode() {
                var $this = this;
                let reg = /^1\d{10}$/;
                if (!(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test($('.phoneNumber input').val()))) {
                    Toast("请输入正确的手机号")
                    $(".msgNumberBtn").attr('disabled', true);
                    return false;
                } else {
                    $(".msgNumberBtn").attr('disabled', false);
                }
                $this.phoneNum = $('.phoneNumber input').val();
                if (reg.test($this.phoneNum)) {
                    $.ajax({
                        type: 'POST',
                        url: $this.global.host + 'customer/sendCaptcha',
                        data: {
                            mobile: $this.phoneNum
                        },
                        success: function (response) {
                            $this.codeQC = response.result;
                            $this.isShow = false;
                        }
                    })
                    $this.msgTime();
                } else {
                    Toast("请输入正确的手机号")
                }
            },
            msgTime() {
                this.countTime = 60;
                this.isShow = false;
                let _this = this;
                let timer = setInterval(function () {
                    _this.countTime--;
                    if (_this.countTime == 0) {
                        $(".msgNumberBtn").attr('disabled', false);
                        clearInterval(timer);
                        _this.isShow = true;
                    }
                }, 1000)
            },
            checkVal() {
                if ($('.phoneNumber input').val().length == 11) {
                    $(".msgNumberBtn").attr('disabled', false);
                } else {
                    $(".msgNumberBtn").attr('disabled', true);
                }
                if ($('.phoneNumber input').val().length == 11 && $('.msgNumberInp').val().length == 6) {
                    $('.loginBtn').attr('disabled', false);
                } else {
                    $('.loginBtn').attr('disabled', true);
                }
            },
            login() {
                this.msgNum = $('.msgNumberInp').val();
                var _this = this;
                _this.toLogin();
            },
            toLogin() {
                this.storage = window.localStorage;
                var _this = this;
                $.ajax({
                    type: 'POST',
                    url: _this.global.host + 'customer/login',
                    data: {
                        mobile: $('.phoneNumber input').val(),
                        captcha: _this.msgNum,
                        deviceToken: '111111',
                    },
                    headers: {'Cookie': 'JSESSIONID=481BFC03311A3E46D29BC8F5698A5C29'},
                    emulateJSON: true,
                    success: function (response) {
                        if (response.resultCode === '0000') {
                            let expireDays = 1000 * 60 * 60 * 24 * 15;
                            let obj = {
                                token: response.result.token,
                            };
                            $('.phoneNumber input').val('');
                            $('.msgNumberInp').val('');
                            _this.personInfo = obj;
                            _this.storage.setItem('user', JSON.stringify(_this.personInfo), expireDays);
                            _this.storage.setItem('uuid', JSON.stringify(response.result.user));
                            _this.$router.back(-1);
                        } else {
                            Toast(response.resultMsg);
                        }
                    }
                })
            },
        },
        components: {
            headeReturn
        }
    }
</script>



